<template>
    <div class="home" v-loading="isLoading">
        <el-empty v-if="!user" :description="`欢迎使用北邮志愿系统，请先登录。`" />
        <template v-else>
            <h3>本周日程</h3>
            <time-chart :data="timeChartData" />
        </template>
    </div>
</template>

<script>
import AV from 'leancloud-storage'
import TimeChart from '../components/TimeChart.vue'
let dayjs = require('dayjs')
export default {
    name: 'Home',
    components: { TimeChart },
    data () {
        return {
            isLoading: false,
            user: AV.User.current(),
            timeChartData: {}
        }
    },
    beforeMount () {
        if (!this.user) return
        this.isLoading = true

        let todo_ = AV.User.current()?.get('todo')
        let todo = [], todo__ = []
        todo_.forEach(v => {
            if (dayjs().isSame(dayjs(v.date), 'week')) todo.push(v)
            if (!dayjs().isAfter(dayjs(v.date), 'week')) todo__.push(v) // 偷偷摸摸删掉过期的事件
        })
        AV.User.current()?.set('todo', todo__).save().then(() => {
            let query = new AV.Query('UserActivityMap')
            return query.equalTo('user', this.user).find()
        }).then(records => {
            let activity_ = records.map(v => v.toJSON())
            let activity = []
            activity_.forEach(v => {
                for (let i in v.joined) {
                    let j = v.joined[i]
                    if (dayjs().isSame(dayjs(v.periods[j].date), 'week')) {
                        let data = v.periods[j]
                        data.aid = v.objectId
                        data.aname = v.title
                        activity.push(v.periods[j])
                    }
                } 
            })
            this.timeChartData = {
                week: AV.User.current().get('schedule'), todo, activity
            }
            this.isLoading = false
        })
    }
}
</script>
