<template>
  <v-chart class="chart" :option="option" />
</template>

<script>
import VChart from 'vue-echarts'
import { use, graphic } from 'echarts/core'
import { CustomChart } from 'echarts/charts'
import { TitleComponent, GridComponent, TooltipComponent, LegendComponent } from 'echarts/components'
import { CanvasRenderer } from 'echarts/renderers'
let dayjs = require('dayjs')
use([ TitleComponent, CustomChart, GridComponent, TooltipComponent, CanvasRenderer, LegendComponent ])

function m2HHmm(m) {
  let HH = Math.floor(m / 60)
  let mm = m % 60
  HH = `0${HH}`.slice(-2)
  mm = `0${mm}`.slice(-2)
  return `${HH}:${mm}`
}

function HHmm2m(HH, mm) {
  return 60 * HH + mm
}

function renderItem(args, api) {
  let categoryIndex = api.value(0)
  let start = api.coord([api.value(1), categoryIndex])
  let end = api.coord([api.value(2), categoryIndex])
  let height = api.size([0, 1])[1] * (api.value(3) == 'current' ? .8 : .6)

  let rectShape = graphic.clipRectByRect({
    x: start[0],
    y: start[1] - height / 2,
    width: end[0] - start[0],
    height: height,
  }, {
    x: args.coordSys.x,
    y: args.coordSys.y,
    width: args.coordSys.width,
    height: args.coordSys.height
  })

  if (api.value(3) != 'current')
    rectShape.r = 3

  return rectShape && {
    type: 'rect',
    shape: rectShape,
    style: api.style()
  }
}

export default {
  name: 'TimeChart',
  components: { VChart },
  props: { data: Object },
  watch: {
    data (v) {
      let weekData = []
      v.week?.forEach((v, weekday) => {
        weekData = weekData.concat(v.map(u => [weekday, HHmm2m(...u[0]), HHmm2m(...u[1]), 'week']))
      })

      let todoData = []
      v.todo?.forEach(v => {
        todoData.push([
          dayjs(v.date).day(),
          HHmm2m(dayjs(v.time[0]).hour(), dayjs(v.time[0]).minute()),
          HHmm2m(dayjs(v.time[1]).hour(), dayjs(v.time[1]).minute()),
          'todo', v.name
        ])
      })

      let activityData = []
      v.activity?.forEach(v => {
        activityData.push([
          dayjs(v.date).day(),
          HHmm2m(dayjs(v.time[0]).hour(), dayjs(v.time[0]).minute()),
          HHmm2m(dayjs(v.time[1]).hour(), dayjs(v.time[1]).minute()),
          'activity', v.name, v.aname, v.aid
        ])
      })
      
      let date = dayjs()
      let currentData = [[
        date.day(),
        HHmm2m(date.hour(), Math.max(date.minute() - 3, 0)),
        HHmm2m(date.hour(), Math.min(date.minute() + 3, 60)),
        'current'
      ]]

      this.option.series[0].data = weekData
      this.option.series[1].data = todoData
      this.option.series[2].data = activityData
      this.option.series[3].data = currentData
    }
  },
  data () {
    let weekday = ['周日', '周一', '周二', '周三', '周四', '周五', '周六']
    return {
      option: {
        title: { show: false },
        grid: { height: 300, top: 40 },
        xAxis: {
          min: 360, max: 1320, interval: 120,
          axisLabel: { formatter: v => m2HHmm(v) },
          position: 'top'
        },
        yAxis: { data: weekday, inverse: true },
        color: [ '#409EFF', '#67C23A', '#E6A23C', '#F56C6C' ],
        legend: { data: [ '课程表', '其他日程', '志愿工作', '目前时间' ], bottom: 20 },
        tooltip: { formatter: args => {
          switch (args.value[3]) {
            case 'week':
              return `【课程】${ weekday[args.value[0]] } ${ m2HHmm(args.value[1]) } - ${ m2HHmm(args.value[2]) }`
            case 'todo':
              return `【日程】${ weekday[args.value[0]] } ${ m2HHmm(args.value[1]) } - ${ m2HHmm(args.value[2]) }
              ${ args.value[4] ? `（${ args.value[4]}）` : '' }`
            case 'activity':
              return `【日程】${ weekday[args.value[0]] } ${ m2HHmm(args.value[1]) } - ${ m2HHmm(args.value[2]) }
              （${ args.value[5] }${ args.value[4] ? ` - ${ args.value[4] }` : '' }）`
            case 'current':
              return '目前时间'
          }
        }},
        series: [{
          type: 'custom',
          name: '课程表',
          renderItem: (args, api) => renderItem(args, api)
        }, {
          type: 'custom',
          name: '其他日程',
          renderItem: (args, api) => renderItem(args, api)
        }, {
          type: 'custom',
          name: '志愿工作',
          renderItem: (args, api) => renderItem(args, api)
        }, {
          type: 'custom',
          name: '目前时间',
          renderItem: (args, api) => renderItem(args, api)
        }]
      }
    }
  }
}
</script>

<style scoped>
.chart {
  height: 400px;
}
</style>