<template>
    <h2>北邮志愿</h2>
    <div id="nav">
        <span>
            <router-link to="/">首页</router-link>
        </span>
        <span v-if="isLogin">
            <router-link to="/activities">活动</router-link>
            <router-link to="/profile">帐户</router-link>
            <a @click="logout" href="javascript: void(0);">登出</a>
        </span>
        <span v-else>
            <router-link to="/login">登陆</router-link>
        </span>
        <span v-if="isAdmin">
            <router-link to="/create">发布</router-link>
        </span>
    </div>
    <router-view v-if="isRouterAlive" />
</template>

<script>
import AV from 'leancloud-storage';

export default {
    data () {
        return {
            isLogin: AV.User.current(),
            isAdmin: AV.User.current()?.get('isAdmin'),
            isRouterAlive: true
        }
    },
    beforeMount () {
        this.$watch(
            () => this.$route.fullPath,
            () => {
                this.isLogin = AV.User.current()
                this.isAdmin = AV.User.current()?.get('isAdmin')
            }
        )
    },
    methods: {
        logout () {
            AV.User.logOut().then(() => {
                this.isLogin = false
                this.$message({message: '登出成功。', type: 'success',})
                this.$router.push('/')
            }).catch(e => {
                this.$message({
                    message: `[${e.code}] ${e.rawMessage}`,
                    type: 'warning',
                })
            })
        },
        refresh () {
            this.isRouterAlive = false
            this.$nextTick(() => (this.isRouterAlive = true))
        }
    }
}
</script>


